<template>
  <div class="login_page">
    <transition name="form-fade" mode="in-out">
      <section class="form_contianer">
        <!-- <img src="../assets/img/login.png" style="width:1035px;height:881px" alt=""> -->
        <el-form :model="loginForm" :rules="rules" ref="loginForm" class="loginForm">
          <div class="serverUrl">
            <el-radio v-model="serverUrl" :label="pattern.off">离线</el-radio>
            <el-radio v-model="serverUrl" :label="pattern.on">在线</el-radio>
            <!-- <el-radio v-model="serverUrl" label="http://192.168.1.108:8088">在线二</el-radio> -->
          </div>
          <el-form-item prop="username" class="login-item">
            <span class="loginTips">
              <i class="el-icon-user-solid"></i>
            </span>
            <el-input
              @keyup.enter.native="submitForm('loginForm')"
              class="area"
              type="text"
              placeholder="用户名"
              v-model="loginForm.name"
            ></el-input>
          </el-form-item>
          <el-form-item prop="password" class="login-item">
            <span class="loginTips el-icon-key loginTips2" ></span>
            <el-input
              @keyup.enter.native="submitForm('loginForm')"
              class="area"
              type="password"
              placeholder="密码"
              v-model="loginForm.passwd"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('loginForm')" class="submit_btn">登 录</el-button>
          </el-form-item>
          <div class="sanFangArea">
            <p class="title">第三方账号登录</p>
            <ul class="rflex">
              <li @click="loginByWechat">
                <icon-svg icon-class="iconwechat" />
              </li>
              <li>
                <icon-svg icon-class="iconweibo" />
              </li>
              <li>
                <icon-svg icon-class="iconGithub" />
              </li>
            </ul>
          </div>
        </el-form>
      </section>
    </transition>
  </div>
</template>

<script>
import logoImg from "@/assets/img/logo.png";
import { login } from "@/api/user";
import { setToken } from "@/utils/auth";
export default {
  data() {
    return {
      pattern:window.pattern,
      serverUrl: window.pattern.on,//,"http://103.40.21.189:8088",
      logo: logoImg,
      loginForm: {
        name: "admin",
        passwd: "123456"
      },
      rules: {
        name: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 2, max: 8, message: "长度在 2 到 8 个字符", trigger: "blur" }
        ],
        passwd: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  mounted() {
    this.sessionData("clean","user")
  },
  methods: {
    getConfig(){

    },
    loginByWechat() {},
    showMessage(type, message) {
      this.$message({
        type: type,
        message: message
      });
    },
    submitForm(loginForm) {
      this.sessionData("set","serverUrl",this.serverUrl)
      window.baseURL = this.serverUrl;
       console.log("这个是链接",window.baseURL);
      this.$refs[loginForm].validate(valid => {
        if (valid) {
          const userinfo = {
            name: this.loginForm.name,
            passwd: this.loginForm.passwd
          };
          login(userinfo).then(res => {
            if (res.code == 0) {
              var user = this.$unfly(res.data);
              this.sessionData("set", "user", user);
              setToken("Token", user.token);
              this.$router.push({ path: "/index" });
              window.changeRouter();
              // setTimeout(()=>{
              //   window.location.reload();
              // },100)
            }
          });
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
/deep/.serverUrl {
  margin-bottom: 20px;
}
/deep/.serverUrl .el-radio__label {
  color: #fff !important;
}
.login_page {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url(../assets/img/bg9.jpg) no-repeat center center;
  background-size: 100% 100%;
}
.form_contianer {
  position: absolute;
  top: 38%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: url(../assets/img/login.png) no-repeat center center;
  background-size: 940px 800px;
  width: 631px;
  height: 455px;
  border-radius: 5px;
  padding: 462px 160px 0 244px;
  text-align: center;
  .titleArea {
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    font-size: 22px;
    width: 100%;
    padding-bottom: 20px;
    .title {
      i {
        color: #ff6c60;
      }
    }
  }

  .loginForm {
    .submit_btn {
      width: 100%;
      padding: 13px 0;
      font-size: 16px;
    }
    .loginTips2{
      color: #0df0fe !important;
      font: 18px;
    }
    .loginTips {
      position: absolute;
      left: 10px;
      z-index: 20;
      font-size: 18px;
      top: 50%;
      transform: translateY(-50%);
      i {
        color: #0df0fe !important;
      }
    }
  }
}

.manage_tip {
  margin-bottom: 20px;
  .title {
    font-family: cursive;
    font-weight: bold;
    font-size: 26px;
    color: #fff;
  }
  .logo {
    width: 60px;
    height: 60px;
  }
}

.tiparea {
  text-align: left;
  font-size: 12px;
  color: #333;
  padding: 10px 0;
  .tip {
    margin-left: 54px;
  }
}

.form-fade-enter-active,
.form-fade-leave-active {
  transition: all 1s;
}
.form-fade-enter,
.form-fade-leave-active {
  transform: translate3d(0, -50px, 0);
  opacity: 0;
}
.loginForm {
  .el-button--primary {
    background-color: #409eff;
    border: 1px solid #409eff;
  }
}
.sanFangArea {
  border-top: 1px solid #dcdfe6;
  padding: 10px 0;
  display: none;
  .title {
    font-size: 14px;
    color: #8b9196;
    margin-bottom: 10px;
  }
  ul {
    li {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      .svg-icon {
        font-size: 24px;
      }
    }
  }
}
/deep/.svg-icon {
  color: #0df0fe !important;
}
/deep/.el-form-item__content {
  width: 559px;
  height: 57px !important;
}
/deep/.el-input--mini input {
  width: 559px;
  height: 57px !important;
  border-radius: 0px !important;
  border: none;
  color: #fff;
  background: rgba(38, 128, 180, 0.7);
}
/deep/.el-input--mini .el-input__inner {
  padding-left: 40px !important;
}
/deep/.loginForm .submit_btn {
  width: 559px;
  height: 57px !important;
  border-radius: 0px !important;
  border: none;
  color: #fff;
  background: rgba(38, 128, 180, 1);
}
/deep/.loginForm .el-input__inner {
  padding-left: 30px !important;
  height: 36px !important;
  height: 57px !important;
}
</style>
